<template>
  <div>
    <!-- header -->
    <div>
      <van-nav-bar title="搜索结果" left-arrow fixed @click-left="onClickLeft" />
    </div>
    <!-- 搜索结果 -->
    <div style="padding-top: 1.22667rem">
      <van-list v-model="loading" :finished="finished" finished-text="没有更多了" offset="50" @load="onLoad">
        <ArticleItem
          v-for="item in resultList"
          :key="item.art_id"
          :id="item.art_id"
          :title="item.title"
          :pubdate="item.pubdate"
          :author="item.aut_name"
          :comm-count="item.comm_count"
          :cover="item.cover"
          :isCrossShow="false"
          @click.native="$router.push(`/article_detail?aid=${item.art_id}`)"
        />
      </van-list>
    </div>
  </div>
</template>

<script>
import ArticleItem from '@/components/ArticleItem'
export default {
  name: 'SearchResult',
  components: {
    ArticleItem
  },
  data() {
    return {
      page: 1,
      per_page: 10,
      resultList: [],
      loading: false,
      finished: false
    }
  },
  methods: {
    onClickLeft() {
      this.$router.back()
    },
    // 获取搜索结果列表的方法
    async getResultList() {
      const { page, per_page, keyword, resultList } = this
      const res = await this.$API.reqSearchResult({ page, per_page, keyword })
      this.loading = false
      const { results } = res.data.data
      // 判断数据是否用完
      if (results.length === 0) return (this.finished = true)
      this.resultList = [...resultList, ...results]
      // 页码+1
      this.page++
    },
    onLoad() {
      this.getResultList()
    }
  },

  computed: {
    keyword() {
      return this.$route.params.keyword
    }
  }
}
</script>

